<template>
  <div class="banner">
    <div class="container">
      <ul>
        <li>
          <routerLink to="/">
            <el-image :src="bannerUrl" lazy></el-image>
          </routerLink>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bannerList: [],
      bannerUrl:
        "http://yjy-xiaotuxian-dev.oss-cn-beijing.aliyuncs.com/picture/2021-04-15/1ba86bcc-ae71-42a3-bc3e-37b662f7f07e.jpg",
      timer: null,
    };
  },
  watch: {
    bannerList: {
      handler() {
        let i = 0;
        this.timer = setInterval(() => {
          this.bannerUrl = this.bannerList[i].imgUrl;
          i++;
          if (i === this.bannerList.length) i = 0;
        }, 3000);
      },
    },
  },
  created() {
    this.getBannerList();
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    async getBannerList() {
      const { data } = await this.$axios.get("/home/banner");
      // console.log(data);
      this.bannerList = data.result;
    },
  },
};
</script>

<style lang="less" scoped>
.banner {
  ul {
    .el-image {
      width: 100%;
    }
  }
}
</style>