<template>
  <div class="hot">
    <div class="container">
      <div class="head">
        <h3>人气推荐<small>人气爆款 不容错过</small></h3>
      </div>
      <div class="main">
        <ul>
          <li v-for="item in hotList" :key="item.id">
            <routerLink to="/">
              <el-image :src="item.picture" lazy></el-image>
              <p class="name">{{ item.title }}</p>
              <p class="desc">{{ item.alt }}</p>
            </routerLink>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      hotList: [],
    };
  },
  methods: {
    async getHotList() {
      const { data } = await this.$axios.get("/home/hot");
      // console.log(data);
      this.hotList = data.result;
    },
  },
  created() {
    this.getHotList();
  },
};
</script>

<style lang="less" scoped>
.hot {
  background-color: #ffffff;
  .head {
    padding: 40px 10px;
    display: flex;
    align-items: flex-end;
    h3 {
      flex: 1;
      font-size: 32px;
      font-weight: normal;
      height: 35px;
      line-height: 35px;
      small {
        font-size: 16px;
        color: #909399;
        margin-left: 20px;
      }
    }
  }
  .main {
    ul {
      display: flex;
      justify-content: space-between;
      height: 406px;
      li {
        width: 306px;
        height: 406px;
        .el-image {
          width: 306px;
          height: 306px;
        }
        p {
          font-size: 22px;
          padding-top: 12px;
          text-align: center;
        }
        .desc {
          color: #909399;
          font-size: 18px;
        }
      }
    }
  }
}
</style>