<template>
  <div class="goods">
    <div class="container" v-for="item in goodsList" :key="item.id">
      <div class="head">
        <h3>
          {{ item.name }}<small>{{ item.saleInfo }}</small>
        </h3>
        <routerLink :to="`/category/${item.id}`" class="more">
          <span>查看全部<i class="el-icon-arrow-right"></i></span>
        </routerLink>
      </div>
      <div class="main">
        <div class="cover">
          <routerLink to="/">
            <el-image :src="item.picture" lazy></el-image>
          </routerLink>
        </div>
        <ul>
          <li v-for="subitem in item.goods" :key="subitem.id">
            <div class="item">
              <routerLink :to="`/goods/${subitem.id}`">
                <div class="image">
                  <el-image :src="subitem.picture" lazy></el-image>
                </div>
              </routerLink>
              <p class="name ellipsis-2">
                {{ subitem.name }}
              </p>
              <p class="desc ellipsis">{{ subitem.desc }}</p>
              <p class="price">&yen;{{ subitem.price }}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      goodsList: [],
    };
  },
  methods: {
    async getGoodsList() {
      const { data } = await this.$axios.get("/home/goods");
      // console.log(data);
      this.goodsList = data.result;
    },
  },
  created() {
    this.getGoodsList();
  },
};
</script>

<style lang="less" scoped>
.goods {
  background-color: #ffffff;
  .head {
    padding: 40px 10px;
    display: flex;
    align-items: flex-end;
    h3 {
      flex: 1;
      font-size: 32px;
      font-weight: normal;
      height: 35px;
      line-height: 35px;
      small {
        font-size: 16px;
        color: #909399;
        margin-left: 20px;
      }
    }
    .more {
      span {
        font-size: 16px;
        color: #909399;
        &:hover {
          color: #409eff;
        }
      }
    }
  }
  .main {
    display: flex;
    .cover {
      margin-right: 10px;
      .el-image {
        width: 240px;
        height: 610px;
        object-fit: cover;
      }
    }
    ul {
      width: 990px;
      display: flex;
      flex-wrap: wrap;
      li {
        width: 240px;
        height: 300px;
        margin-right: 10px;
        margin-bottom: 10px;
        &:nth-last-child(-n + 4) {
          margin-bottom: 0;
        }
        &:nth-child(4n) {
          margin-right: 0;
        }
        .item {
          width: 240px;
          height: 300px;
          padding: 10px 30px;
          transition: all 0.5s;
          &:hover {
            transform: translate3d(0, -10px, 0);
            box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
          }
          .image {
            width: 160px;
            height: 160px;
            margin: 0 auto;
            .el-image {
              width: 100%;
              height: 100%;
            }
          }
          p {
            margin-top: 6px;
            font-size: 16px;
            &.name {
              height: 44px;
            }
            &.desc {
              color: #606266;
              height: 22px;
            }
            &.price {
              margin-top: 10px;
              font-size: 20px;
              color: #cf4444;
            }
          }
        }
      }
    }
  }
}
</style>