<template>
  <div class="new">
    <div class="container">
      <div class="head">
        <h3>新鲜好物<small>新鲜出炉 品质靠谱</small></h3>
      </div>
      <div class="main">
        <ul>
          <li v-for="item in newList" :key="item.id">
            <routerLink :to="`/goods/${item.id}`">
              <el-image :src="item.picture" lazy></el-image>
              <p class="name ellipsis">{{ item.name }}</p>
              <p class="price">&yen;{{ item.price }}</p>
            </routerLink>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newList: [],
    };
  },
  methods: {
    async getNewList() {
      const { data } = await this.$axios.get("/home/new");
      // console.log(data);
      this.newList = data.result;
    },
  },
  created() {
    this.getNewList();
  },
};
</script>

<style lang="less" scoped>
.new {
  background-color: #ffffff;
  .head {
    padding: 40px 10px;
    display: flex;
    align-items: flex-end;
    h3 {
      flex: 1;
      font-size: 32px;
      font-weight: normal;
      height: 35px;
      line-height: 35px;
      small {
        font-size: 16px;
        color: #909399;
        margin-left: 20px;
      }
    }
  }
  .main {
    ul {
      display: flex;
      justify-content: space-between;
      height: 406px;
      li {
        width: 306px;
        height: 406px;
        background: #f0f9f4;
        .el-image {
          width: 306px;
          height: 306px;
        }
        p {
          font-size: 22px;
          padding: 12px 30px 0 30px;
          text-align: center;
        }
        .price {
          color: #cf4444;
        }
      }
    }
  }
}
</style>